.btn {
  outline:none;
  cursor: pointer;
  display: inline-block;
  letter-spacing: 0.075rem;
  padding: .8rem 1rem;
  margin: auto 2rem;
  position: relative;
  align-self: center;
  text-transform: uppercase;
  border: 3px rgb(16,24,50) solid;
  z-index: 1;
  box-shadow: -0.5rem .5rem transparentize(rgb(16,24,50),1);
  transform-origin: rigth top;
  transition:all 200ms ease-in-out;
  /* width: 6rem */
} 
.btn::before,::after {
    display: block;
    position: absolute;
    z-index: -1;
  }
  
  .btn::before {
    content: '';
    border-image-slice: 1 1 0 1;
    left: -0.59rem; top: .15rem;
    width: .3rem;
    height: 101%;
    transform: skewY(-45deg);
    /* border: 3px rgb(16,24,50) solid; */
  }
  
  .btn::after {
     content: '';
    border-image-slice: 1 1 1 0;
    bottom: -0.61rem; right: 0.16rem;
    width: 101%;
    height: .3rem;
    transform: skewX(-45deg);
    /* border: 3px rgb(16,24,50) solid; */
  }
  
  .btn:active {
    
    border-image-slice: 1;
    background-color: #C2EFF5;
    background-size: 90%;
    transform: translate(-0.5rem,0.5rem);
    /* border: 3px rgb(16,24,50) solid; */
  }

.cta {
  border-image-slice: 1;
  color: white;
} 
.cta::before {
    border-image-slice: 1;
    left: -0.75rem;
    top: .15rem;
  }
  
  .cta::after {
    border-image-slice: 1;
    bottom: -0.75rem;
    right: .15rem;
  }
  
  .cta:hover {
    background:#C2EFF5;
    border-image-slice: 1;
  }  
    
  .cta:hover::before {
      height: 101%;
    }
    .cta:hover::after {
      width: 101%;
    }

.bg {
  font-weight: 500;
  font-size: 1rem;
  background-color: #C2EFF5;
  /* border: 3px solid rgb(16,24,50); */
  color:rgb(16,24,50);
}
  .bg::before,::after {
    background: rgb(16,24,50);
    border: 2.4px solid rgb(16,24,50);
  }
  
  .bg:hover {
    border-image: none;
  }
    .bg:hover::before,::after {
      background:rgb(16,24,50);
    }
